export default {
  name: 'MainMenu',

  data() {
    return {
      activeItem: this.$route.name,
      menu: [{
        path: '/',
        name: 'Home'
      }, {
        path: '/programming',
        name: 'Programming'
      }, {
        path: '/games',
        name: 'Games'
      }, {
        path: '/anime',
        name: 'Anime'
      }, {
        path: '/movies',
        name: 'Movies'
      }, {
        path: '/books',
        name: 'Books'
      }, {
        path: '/photography',
        name: 'Photography'
      }, {
        path: '/contacts',
        name: 'Contacts'
      }]
    };
  },

  watch: {
    '$route.name': function $routeName() {
      this.updateActiveItem();
    }
  },
  methods: {
    updateActiveItem() {
      this.activeItem = this.$route.name;
    },

    isActive(menuItem) {
      return this.activeItem === menuItem;
    },

    setActive(menuItem) {
      this.activeItem = menuItem;
    }

  }
};